import style from "./Friend.module.css"

const Friend = () => {
    return (
        <div>
            <img className={style.avatar} src="data:image/jpeg;base64,"
                 alt="avatar"/>
            <span className={style.username}>Friend name</span><br/>
            <span className={style.status}>Online/Offline</span>
        </div>
    )
}

export default Friend
